body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica";
 /* font-family: system-ui, -apple-system, BlinkMacSystemFont, "Helvetica Neue",
    "Lucida Grande", "Segoe UI";*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
	font-family: "Helvetica";
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;before */
}

/* header */
.title-box {
  color: #fff;
  font-size: 23px;
  padding-left: 25px;
  padding-top: 30px;
  width: 125px;
  font-family: "Helvetica";
  text-align: left;
}

.search-box {
  font-size: 20;
  display: "block";
  width: "95%";
  margin-top: 20px;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: "0.3px solid #ffffff";
  margin-left: "30px";
  background-color: "#313541";
  color: "white";
  font-family: "Helvetica";

}
.App {
  text-align: center;
}
.App a {
  float: left;
  color: white;
  font-size: 24px;
  font-family: "Helvetica";
  margin-top: 3%;
  padding-bottom: 20px;
  padding-left: 30px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
/* filter buttons */
.button-div {
  padding-top: 20px;
  text-align: center;
  padding-left: 25px;
}
.button-div button {
  width: 120px;
  height: 30px;
  color: #fff;
  font-style: bold;
  font-family: "Helvetica";
  background: transparent;
  border-color: aqua;
  border-radius: 7%;
  margin: 5px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
/* scrollbar style */
::-webkit-scrollbar {
  width: 4px;
  border: 1px solid #8091a5;
}

::-webkit-scrollbar-track {
  border-radius: 0;

  background: #8091a5;
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background: #50e3c2;
}

