/*Text over image*/
.hoverText {
  font-size: 12px;
  position: absolute;
  width: 180px;
  max-height: 150px;
  overflow: hidden;
  top: 20%;
  left: 5%;
  background: transparent;
  padding: 20px;
  display: none;
}
.hoverText p {
  color: #f5f9fc;
  font-size: 12px;
  position: relative;
}
.poster-container {
  position: relative;
}

.poster-container:hover {
  opacity: 0.5;
}
.poster-container:hover .hoverText {
  display: block;
}

.movie-poster {
  position: relative;
  height: 278px;
}
.movie-poster img {
  width: 185px;
  height: 278px;
  padding-top: 10px;
}
.title {
  width: 180px;
  height: 65px;
  font-size: 15px;
  text-align: left;
  color: #50e3c2;
  overflow: hidden;
  margin-top: -9px;
}
.title span {
  font-size: 10px;
}
.vote {
  border-radius: 5%;
  background: #3c4665;
  color: #50e3c2;
  font-size: 24px;
  text-align: left;
  height: 33px;
  padding-top: 6px;
  padding-left: 10px;
  padding-right: 10px;
  width: 160px;
}
.btn-remove {
  border-radius: 5%;
  background: #50e3c2;
  color: #2b3247;
  font-size: 15px;
  text-align: center;
  height: 35px;
  margin-top: 10px;
  width: 180px;
  border: none;
  font-style: bold;
}
/* modal-box */
.modal {
  color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
}
.modal-background {
  display: none;
}
.back-btn {
  text-align: left;
  background: transparent;
  color: #50e3c2;
  border: none;
  margin-right: 800px;
}
.modal .modal-main {
  position: relative;
  background-color: #313541;
  width: 84%;
  margin-left: 8%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 1;
}
.modal .trailermodal-main {
  position: relative;
  text-align: right;
  width: 80%;
  margin-left: -20%;
  margin-top: 10%;
  height: 100%;
  overflow: hidden;
  top: 50%;
  left: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

