/* header */
.watchlist-title-box {
  color: #fff;
  font-size: 26px;
  padding-left: 25px;
  padding-top: 30px;
  width: 150px;
  text-align: left;
}
.search-box {
  font-size: 20;
  display: "block";
  width: "95%";
  margin-top: 40;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: "0.3px solid #ffffff";
  margin-left: "30px";
  background-color: "#313541";
  color: "white";
  font-family: "Helvetica";
}
/* scrollbar style */
::-webkit-scrollbar {
  width: 4px;
  border: 1px solid #8091a5;
}

::-webkit-scrollbar-track {
  border-radius: 0;

  background: #8091a5;
}

::-webkit-scrollbar-thumb {
  border-radius: 0;
  background: #50e3c2;
}

/* side navigation bar style */
.sidenav {
  height: 100%;
  width: 100px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #50e3c2;
  overflow-x: hidden;
  padding-top: 80px;
}

.sidenav a {
  padding: 6px 8px 0px 23px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.main {
  font-family: "Helvetica";
  margin-left: 100px; /* Same as the width of the sidenav */
  font-size: 18px; /* Increased text to enable scrolling */
  padding: 0px 10px;
}

